.case{
    &-slider{
        padding: 0;
        max-width: 100%!important;
    }
    &-item{
        display: block;
        text-decoration: none;
        background-size: cover;
        background-repeat: no-repeat;
        min-height: 253px;
        border-radius: 20px;
        overflow: hidden;
        @include sm{
            min-height: 220px;
        }
        &:not(:last-child){
            margin-bottom: 30px;
            @include md{
                margin-bottom: 15px;
            }
        }
        position: relative;
        &:before{
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            pointer-events: none;
            background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 78.12%);
        }
        &-title{
            position: absolute;
            left: 0;
            bottom: 0;
            z-index: 2;
            padding: 0 35px 20px 35px;
            font-size: 20px;
            font-weight: bold;
            @include md{
                font-size: 18px;
            }
            @include sm{
                padding: 23px;
                font-size: 16px;
            }
            &.large{
                @media screen and (min-width: $lg) {
                    font-size: 32px;
                    padding: 0 47px 35px 47px;
                }
            }
        }
    }
}