.box{
    box-shadow: 0px 4px 9.9px 2px rgba(61, 61, 61, 0.17);
    border-radius: 26px;
    @include sm{
        border-radius: 15px;
    }
}

.rating{
   display: flex;
   align-items: center;
   gap: 4px; 
   &-star{
    width: 17px;
    height: 17px;
    background-image: url("../img/star.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
   }
}

.product{
    padding: 45px;
    box-sizing: border-box;
    @include sm{
        padding: 30px;
    }
    &-name{

    }
    &-img{
        width: 230px;
        height: 230px;
        margin: 0 auto 35px auto;
        @include sm{
            width: 200px;
            height: 200px;
            margin: 0 auto 25px auto;
        }
        img{
            height: inherit;
        }
    }
    &-name,
    &-price{
        margin-bottom: 50px;
        line-height: 1.2;
        font-size: 32px;
        @include lg{
            font-size: 26px;
            margin-bottom: 40px;
        }
        @include md{
            font-size: 22px;
            margin-bottom: 30px;
        }
        @include sm{
            font-size: 20px;
            margin-bottom: 20px;
        }
    }
    &-footer{
        .button{
            &:not(:last-child){
                margin-bottom: 15px;
            }
        }
    }
}

.socials{
    gap: 22px;
    &-item{}
    &-link{
        background-color: var(--white);
        width: 61px;
        height: 61px;
        border-radius: 100%;
        img{
            max-width: 35px;
            width: 100%;
        }
    }
    &-small{
        gap: 10px;
        li{
            a{
                width: 19px;
                height: 19px;
                img{
                    max-width: 19px;
                }
            }
        }
    }
}

.tags{
    gap: 30px;
    @include lg{
        gap: 15px;
    }
    @include sm{
        gap: 10px;
    }
    li{
        a{
            display: inline-flex;
            align-items: center;
            text-decoration: none;
            min-height: 48px;
            box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
            background-color: var(--white);
            border-radius: 40px;
            padding-left: 30px;
            padding-right: 30px;
            transition: background-color ease-in-out 0.2s,
                color ease-in-out 0.2s,
                box-shadow ease-in-out 0.2s;
            &:hover,
            &.current{
                background-color: var(--red);
                color: var(--white);
                box-shadow: none;
            }
            @include lg{
                min-height: 40px;
            }
        }
    }
}