.section{
  padding-top: var(--sec-p);
  padding-bottom: var(--sec-p);

  &.pt-double{
    padding-top: calc(var(--sec-p) * 2);
  }
  &.pb-double{
    padding-bottom: calc(var(--sec-p) * 2);
  }

  &.pt-half{
    padding-top: calc(var(--sec-p) * 1.5);
  }
  &.pb-half{
    padding-bottom: calc(var(--sec-p) * 1.5);
  }

  &.has-img-bg{
    background-position: center;
    background-size: cover;
    position: relative;

    background-attachment: fixed;

    &:before{
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      pointer-events: none;
      background-color: var(--blue);
      opacity: 0.5;
    }
    & > .container{
      position: relative;
      z-index: 2;
    }
  }


  &.has-decor{
    clip-path: polygon(0 0, 100% 10%, 100% 100%, 0 90%);
    @include sm{
      clip-path: polygon(0 0, 100% 5%, 100% 100%, 0 95%);
    }
    @include md{
      padding-top: calc(var(--sec-p) * 2);
      padding-bottom: calc(var(--sec-p) * 2);
    }
  }

  &-header{
    margin-bottom: var(--sec-h-mb);
  }
  &-title{
    margin-bottom: var(--sec-h-t-mb);
    &:last-child{
      margin: 0;
    }
  }
  &-desc{
    font-size: 32px;
    line-height: 1.2;

    &:last-child{
      margin: 0;
    }

    @include xl{
      line-height: 1.4;
      font-size: 24px;
    }
    @include lg{
      font-size: 20px;
    }
    @include sm{
      font-size: 16px;
    }
  }

  &-footer{
    margin-top: 40px;
    font-size: 16px;
    @include md{
      font-size: 14px;
      margin-top: 20px;
    }
  }
  &-filter{
    margin-bottom: 35px;
    @include sm{
      margin-bottom: 30px;
    }
  }
  &-controls{
    gap: 30px;
    button{
      width: 48px;
      height: 48px;
    }
  }
}

