.po{
    @media screen and (min-width: $lg) {
        padding-top: calc(var(--sec-p) * 3);
        padding-bottom: calc(var(--sec-p) * 2);
    }
    @include lg{
        padding-top: calc(var(--sec-p) * 2);
        padding-bottom: calc(var(--sec-p) * 2);
    }
    .section-desc{
        max-width: 981px;
        display: inline-block;
    }
    &-row{}
    &-info{
        border-radius: 40px;
        padding: 45px 30px;
        box-sizing: border-box;
        @include sm{
            padding: 30px;
            border-radius: 15px;
        }
        &-icon{
            width: 98px;
            height: 98px;
            margin: 0 auto 14px auto;
            @include sm{
                width: 65px;
                height: 65px;
            }
        }
        &-title{}
    }
}