html,
body{
	margin: 0;
	padding: 0;
}

body{
	font-family: var(--body-ff);
	font-size: var(--body-fz);
	line-height: var(--body-lh);
	color: var(--body-fc);
	background-color: var(--body-bg);
}

*{
	outline-offset: 3px;
	outline-color: var(--black);
	&::selection {
		color: var(--white);
		background: var(--black);
	}
}
  
h1,
h2,
h3,
h4,
h5,
h6{
	font-family: var(--h-ff);
	font-weight: var(--h-fw);
	line-height: var(--h-lh);
	margin: var(--h-mb);
}
h1,
.h1{
	font-size: var(--h1-fz);
}
h2,
.h2{
	font-size: var(--h2-fz);
}
h3,
.h3{
	font-size: var(--h3-fz);
}
h4,
.h4{
	font-size: var(--h4-fz);
}
h5,
.h5{
	font-size: var(--h5-fz);
}
h6,
.h6{
	font-size: var(--h6-fz);
}

p{
	margin: var(--p-mb);
}

a{
	color: inherit;
	&:hover{
		text-decoration: none;
	}
}

b,
strong{
	font-weight: bold;
}

img{
	max-width: 100%;
	height: auto;
}