// Breakpoints
$xxl: 1440px;
$xl: 1200px;
$lg: 991px;
$md: 768px;
$sm: 576px;

$row-cols: 12;

// Variables
:root{
  // Grid
  --container-width : 1230px;
  --container-gap   : 20px;

  --row-cols        : 12;
  --row-gap-x       : 20px;
  --row-gap-y       : 20px;

  // Body
  --body-ff         : 'Jost', Arial, Helvetica, sans-serif;
  --body-fz         : 18px;
  --body-lh         : 1.4;
  --body-fc         : var(--black-90);
  --body-bg         : var(--white);

  // Heading
  --h-ff            : var(--body-ff);
  --h-fw            : 500;
  --h-lh            : 1.2;
  --h-mb            : 0 0 30px 0;
  --h1-fz           : 50px;
  --h2-fz           : 48px;
  --h3-fz           : 42px;
  --h4-fz           : 36px;
  --h5-fz           : 32px;
  --h6-fz           : 28px;

  // Paragraph
  --p-mb            : 0 0 20px 0;

  // Section
  --sec-p           : 100px;
  --sec-h-mb        : 60px;
  --sec-h-t-mb      : 30px;

  // Colors
  --white     : #ffffff;
  --black     : #2E2D2F;
  --black-90  : #575658;
  --gray      : #CCC5C5;
  --gray-90   : #F3F3F3;
  --blue      : #132D6E;
  --orange    : #FD7228;
  --red       : #CA2A38;
}

@media screen and (max-width: $xl) {
  :root{
    --body-fz: 16px;

    --h1-fz: 42px;
    --h2-fz: 36px;
    --h3-fz: 32px;
    --h4-fz: 28px;
    --h5-fz: 26px;
    --h6-fz: 24px;
  }
}
@media screen and (max-width: $lg) {
  :root{
    --h1-fz: 36px;
    --h2-fz: 32px;
    --h3-fz: 28px;
    --h4-fz: 24px;
    --h5-fz: 22px;
    --h6-fz: 20px;

    --sec-p      : 70px;
    --sec-h-mb   : 40px;
    --sec-h-t-mb : 20px;
  }
}
@media screen and (max-width: $md) {
  :root{
    

    --h1-fz: 32px;
    --h2-fz: 28px;
    --h3-fz: 26px;
    --h4-fz: 22px;
    --h5-fz: 20px;
    --h6-fz: 18px;
  }
}
@media screen and (max-width: $sm) {
  :root{
    --body-fz: 14px;

    --h1-fz: 26px;
    --h2-fz: 24px;
    --h3-fz: 22px;
    --h4-fz: 20px;
    --h5-fz: 18px;
    --h6-fz: 16px;

    --sec-p     : 50px;
    --sec-h-mb  : 25px;
    --sec-h-t-mb: 15px;

  }
}