.callback{
    clip-path: polygon(0 0, 100% 10%, 100% 100%, 0 100%);
    &-line{
        gap: 50px;
        &:not(:last-child){
            margin-bottom: 65px;
            @include md{
                margin-bottom: 25px;
            }
        }
        @include md{
            flex-direction: column;
            gap: 20px;
        }
    }
    &-items{
        gap: 38px;
        @include md{
            gap: 24px;
        }
    }
    &-item{
        display: inline-flex;
        align-items: center;
        justify-content: center;
        gap: 5px;
        min-height: 61px;
        padding-left: 40px;
        padding-right: 40px;
        border-radius: 18px;
        background-color: var(--white);
        color: var(--black-90);
        text-decoration: none;
        &-icon{
            max-width: 32px;
        }
        @include md{
            width: 100%;
        }
    }
    &-sub{
        display: flex;
        align-items: center;
        gap: 33px;
        @include md{
            flex-direction: column-reverse;
            gap: 15px;
        }
        &-inner{
            max-width: 306px;
            width: 100%;
            position: relative;
            @include md{
                max-width: 100%;
            }
        }
        &-control{
            min-height: 61px!important;
            font-size: 20px;
            border-radius: 18px;
        }
        &-submit{
            position: absolute;
            width: 37px;
            height: 37px;
            background-color: transparent;
            border: none;
            cursor: pointer;
            right: 21px;
            top: 50%;
            transform: translate(0, -50%);
        }
        &-footer{
            max-width: 326px;
            font-size: 18px;
            @include md{
                max-width: 100%
            }
        }
    }
}