.hero{
    // clip-path: polygon(0 0, 100% 0, 100% 100%, 0 90%);
    &-row{
        display: flex;
    }
    &-col{
        &:nth-child(1){
            width: 592px;
            @include lg{
                width: 100%;
            }
        }
        &:nth-child(2){
            flex: 1;
        }
    }
    &-content{
        .section-header{
            margin-bottom: 32px;
        }
        .section-desc{
            font-size: 24px;
            max-width: 475px;
            @include lg{
                font-size: 20px;
            }
            @include sm{
                font-size: 16px;
            }
        }
        &-meta{
            margin-top: 70px;
            @include xxl{
                margin-top: 40px;
            }
            &-item{
                margin-bottom: 28px;
            }
        }
    }
    &-img{
        position: relative;
        z-index: 3;
        margin: -20px 0 0 20px;
        @include lg{
            margin: 20px 0;
        }
    }
    &-button{
        max-width: 450px;
        @include lg{
            min-width: 100%;
        }
    }

}