.contract{
    &-row{
        .col{
            width: 20%;
            flex: inherit;
            @include lg{
                width: 33.333%;
            }
            @include sm{
                width: 100%;
            }
        }
    }
    &-box{
        &-icon{
            border-radius: 100%;
            width: 146px;
            height: 146px;
            margin: 0 auto 14px auto;
            @include lg{
                width: 120px;
                height: 120px;
                img{
                    max-width: 50%;
                }
            }
            @include sm{
                width: 98px;
                height: 98px;
                margin: 0 auto 10px auto;
            }
        }
        &-title{
            font-size: 20px;
            @include lg{
                font-size: 18px;
            }
            @include sm{
                font-size: 16px;
            }
        }
    }
    &-desc{
        @media screen and (min-width: $lg) {
            font-size: 20px;
        }
    }
}