.swiper{
    max-width: calc(100% - 160px);
    margin: 0 auto;
    @include sm{
        max-width: calc(100% - 120px);
    }
    &-control{
        z-index: 3;
        width: 46px;
        height: 46px;
        border-radius: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
        background-color: var(--white);
        border: none;
        cursor: pointer;
        transition: box-shadow ease-in-out 0.2s, transform ease-in-out 0.2s, opacity ease-in-out 0.2s;
        &:hover{
            box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.15);
        }
        &.prev{
            left: 0;
        }
        &.next{
            right: 0;
        }
        &.prev,
        &.next{
            position: absolute;
            top: 50%;
            transform: translate(0, -50%);
            &:active{
                transform: translate(0, -50%) scale(0.97);
            }
        }
        &.swiper-button-disabled{
            opacity: 0.3;
            pointer-events: none;
        }
    }
    &-pagination{
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 55px;
        gap: 10px;
        @include sm{
            margin-top: 25px;
        }
        &-bullet{
            display: block;
            cursor: pointer;
            width: 10px;
            height: 10px;
            border-radius: 100%;
            background-color: var(--gray);
            transition: background-color ease-in-out 0.2s;
            &-active{
                background-color: var(--black);
            }
        }
    }

    &-slide{
        height: auto;
    }
}