.spheres{
    &-box{
        padding: 45px 30px;
        box-sizing: border-box;
        border-radius: 10px;
        gap: 15px;
        @include lg{
            padding: 30px;
        }
        @include sm{
            padding: 20px;
        }
        &-img{
            width: 150px;
            height: 150px;
            @include lg{
                width: 120px;
                height: 120px;
            }
            @include sm{
                width: 80px;
                height: 80px;
            }
        }
        &-title{
            flex: 1;
            font-size: 36px;
            @include xl{
                font-size: 32px;
            }
            @include lg{
                font-size: 26px;
            }
            @include sm{
                font-size: 20px;
            }
        }
    }
}