.testimonials{
    &-box{
        padding: 35px 24px;
        box-sizing: border-box;
        &-header{
            gap: 24px;
            @include sm{
                gap: 13px;
            }
        }
        &-img{
            width: 78px;
            height: 78px;
            border-radius: 100%;
            overflow: hidden;
            @include sm{
                width: 54px;
                height: 54px;
            }
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        &-info{
            flex: 1;
        }
        &-name{
            margin-bottom: 4px;
            font-size: 20px;
            @include sm{
                font-size: 18px;
            }
        }

        &-header,
        &-content{
            margin-bottom: 23px;
            @include sm{
                margin-bottom: 18px;
            }
        }
        &-content{
            max-width: 410px;
        }
        &-gallery{
            gap: 18px;
            @include sm{
                gap: 14px;
            }
            &-item{
                display: inline-block;
                width: 100px;
                border-radius: 10px;
                overflow: hidden;
                @include sm{
                    width: 80px;
                }
                img{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    border-radius: 10px;
                }
            }
        }
    }
}