.benefits{
    clip-path: polygon(0 0, 100% 10%, 100% 100%, 0 100%);
    margin-bottom: -100px;
    @include sm{
        clip-path: polygon(0 0, 100% 5%, 100% 100%, 0 100%);
    }
    @include lg{
        margin-bottom: -70px;
    }
    &-inner{

    }
    &-content{
        @media screen and (min-width: $xl) {
            font-size: 20px;
        }
    }
    &-img{
        img{
            max-width: 280px
        }
    }
    &-footer{
        gap: 20px;
        &-title{
            font-size: 32px;
            line-height: 1.2;
            @include xl{
                font-size: 24px;
            }
        }
        &-text{
            display: none;
        }
        @include sm{
            .button{
                width: 100%;
            }
        }
    }
    &-toggle{
        svg{
            &:nth-child(3){
                display: none;
            }
        }
        &.active{
            svg{
                &:nth-child(2){
                    display: none;
                }
                &:nth-child(3){
                    display: block;
                }
            }
        }
    }
    &-text{
        display: none;
        ol{
            padding-left: 21px;
        }
    }
}