.footer{
    padding: 55px 0;
    box-sizing: border-box; 
    font-size: 14px;
    &-row{
        .col-lg-6{
            @include lg{
                order: -1;
            }
        }
    }  
    a{
        text-decoration: none;
        transition: color ease-in-out 0.2s;
        &:hover{
            color: var(--red);
        }
    }
    &-logo{
        margin-bottom: 17px;
        display: inline-block;
        max-width: 170px;
        img{
            width: 100%;
        }
    }
    &-socials{
        margin-bottom: 24px;
    }
    &-contact{
        max-width: 165px;
    }
    &-contact,
    &-nav{
        li{
            &:not(:last-child){
                margin-bottom: 14px;
            }
        }
    }
    &-geo{
        margin-bottom: 29px;
        cursor: pointer;
        @include lg{
            width: 35%;
        }
    }
    &-title,
    &-info{
        &:not(:last-child){
            margin-bottom: 19px;
        }
    }
    &-payments{
        gap: 14px;
        max-width: 210px;
        @include lg{
            margin-top: 25px;
            max-width: 100%;
        }
        img{
            display: block;
            max-height: 20px;
            max-width: 60px;
        }
    }
    &-item{
        @include lg{
            margin-bottom: 25px;
        }
        &-title{
            text-transform: uppercase;
            margin-bottom: 15px;
            font-size: 20px;
            color: var(--black);
        }
        &-content{
            @include lg{
                display: none;
            }
        }
    }

    &-header{
        justify-content: space-between;
    }
    &-bottom{}
    &-sub{
        margin-bottom: 45px;
        .input-group{
            &:not(:last-child){
                margin-bottom: 20px;
            }
        }
    }
    &-meta{
        li{
            &:not(:last-child){
                margin-bottom: 10px;
            }
            a{
                text-decoration: underline;
                &:hover{
                    text-decoration: none;
                }
            }
        }
    }
    &-toggle{
        position: relative;
        cursor: pointer;
        &:after{
            content: "";
            width: 37px;
            height: 37px;
            position: absolute;
            right: 0;
            top: 50%;
            transform: translate(0, -50%);
            background-image: url("../img/footer-toggle.svg");
            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;
            will-change: auto;
            transition: transform ease-in-out 0.2s, background-image ease-in-out 0.2s;
        }
        &.active{
            &:after{
                background-image: url("../img/footer-toggle-active.svg");
                transform: translate(0, -50%) rotate(180deg);
            }
        }
    }
}