.input{
  &-group{
    margin-bottom: 30px;
    position: relative;
  }
  &-label{
    display: block;
    margin-bottom: 20px;
    @include lg{
      margin-bottom: 10px;
    }
  }
  &-req{
    position: absolute;
    right: 0;
    top: 15px;
  }
  &-control{
    font-family: inherit;
    max-width: 100%;
    width: 100%;
    min-height: 70px;
    padding: 0 35px 0 20px;
    box-sizing: border-box;
    outline: none;
    box-shadow: 0px 4px 9.9px 2px rgba(61, 61, 61, 0.17);
    border-radius: 10px;
    border: none;
    background-color: var(--white);
    @include ph(){
      color: var(--s-black);
      opacity: 0.5;
    }
    @include sm{
      min-height: 40px;
      padding: 0 35px 0 15px;
    }
    &-base{
      box-shadow: none;
      border: 1px solid var(--gray);
      min-height: 44px;
    }
    &-border{
      box-shadow: none;
      min-height: 45px;
      border-radius: 0;
      border-bottom: 1px solid var(--gray);
      padding-left: 0!important;
    }
  }
  &-checkbox,
  &-radio{
    display: block;
    margin: 0;
    position: relative;
    input[type="checkbox"],
    input[type="radio"]{
      position: absolute;
      width: 1px;
      height: 1px;
      opacity: 1;
      visibility: hidden;
    }
    span{
      display: block;
      position: relative;
      padding-left: 30px;
      &:before,
      &:after{
        content: "";
        position: absolute;
        width: 20px;
        height: 20px;
        left: 0;
        top: 1px;
        cursor: pointer;
        transition: top ease-in-out 0.2s, left ease-in-out 0.2s, opacity ease-in-out 0.2s, background-color ease-in-out 0.2s;
      }
    }
  }
  &-checkbox{
    input[type="checkbox"]{
      &:checked + span{
        &:before{
          background-color: var(--s-black);
        }
        &:after{
          opacity: 1;
        }
      }
    }
    span{
      &:before{
        border: 1px solid var(--s-black);
      }
      &:after{
        left: 6px;
        top: 11px;
        background-color: var(--s-white);
        width: 2px;
        height: 2px;
        box-shadow: 2px 0 0 var(--s-white), 4px 0 0 var(--s-white), 4px -2px 0 var(--s-white), 4px -4px 0 var(--s-white), 4px -6px 0 var(--s-white), 4px -8px 0 var(--s-white);
        transform: rotate(45deg);
        opacity: 0;
      }
    }
  }
  &-radio{
    input[type="radio"]{
      &:checked + span{
        &:before{
          background-color: var(--s-black);
        }
        &:after{
          opacity: 1;
        }
      }
    }
    span{
      &:before,
      &:after{
        border-radius: 100%;
      }
      &:after{
        width: 12px;
        height: 12px;
        left: 5px;
        top: 6px;
        background-color: var(--s-white);
      }
      &:before{
        border: 1px solid var(--s-black);
      }
    }
  }
}



select{
  &.input-control{
    background-image: url("../img/select-arrow.svg");
    background-repeat: no-repeat;
    background-position: calc(100% - 20px) center;

    -webkit-appearance: none;
    -moz-appearance: none;
    text-indent: 1px;
    text-overflow: '';
    @include sm{
      background-position: calc(100% - 15px) center;
    }

  }
}