.steps{
    overflow: hidden;
    .section-desc{
        max-width: 919px;
    }
    &-items,
    .section-desc{
        @media screen and (min-width: $md) {
            padding-left: 100px;
            box-sizing: border-box;
        }
    }
    &-item{
        display: flex;
        align-items: center;
        gap: 20px;
        position: relative;
        z-index: 2;
        @media screen and (min-width: $md) {
            &:nth-child(2n+2){
                padding-left: 90px;
            }
        }
        &:not(:last-child){
            margin-bottom: 20px;
        }
        &-num{
            font-size: 128px;
            line-height: 1;
            min-width: 73px;
            @include md{
                font-size: 64px;
                min-width: 38px;
            }
        }
        &-content{
            flex: 1;
            position: relative;
            padding: 21px 0 21px 41px;
            box-sizing: border-box;
            @include md{
                padding: 12px 0 12px 20px;
            }
            &:before{
                content: "";
                width: 100vw;
                height: 100%;
                position: absolute;
                left: 0;
                top: 0;
                background-color: var(--red);
                z-index: -1;
                border-radius: 5px;
                transform: skewX(10deg);
            }
        }
        &-title{
            font-size: 24px;
            margin-bottom: 3px;
            font-weight: 500;
            @include md{
                font-size: 20px;
            }
            @include sm{
                font-size: 16px;
            }
        }
        &-desc{}
    }
}