.equipment{
    &-box{
        padding: 34px;
        box-sizing: border-box;
        text-decoration: none;
        @include sm{
            padding: 30px;
        }
        &-name{
            margin-bottom: 30px;
            font-size: 32px;
            @include lg{
                font-size: 24px;
                margin-bottom: 20px;
            }
            @include sm{
                font-size: 20px;
            }
        }
        &-img{
            margin: 0 auto 30px auto;
            width: 270px;
            height: 270px;
            @include lg{
                width: 200px;
                height: 200px;
                margin: 0 auto 20px;
            }
            @include sm{
                width: 170px;
                height: 170px;
            }
        }
    }
}