.certificates{
    .swiper-slide{
        padding: 10px;
        box-sizing: border-box;
    }
    &-box{
        border-radius: 10px;
        box-shadow: 0px 0px 9px 3px rgba(61, 61, 61, 0.19);
        border-radius: 20px;
        overflow: hidden;
        img{
            border-radius: 20px;
        }
    }
}