.advantages{
    clip-path: polygon(0 0, 100% 10%, 100% 100%, 0 100%);
    margin-top: -190px;
    position: relative;
    z-index: 2;
    background: var(--white);
    @include sm{
        clip-path: polygon(0 0, 100% 5%, 100% 100%, 0 100%);
    }
    @include xl{
        margin-top: -50px;
    }
    &-box{
        text-align: center;
        @include md{
            display: flex;
            align-items: center;
            gap: 20px;
            text-align: left;
        }
        &-icon{
            width: 96px;
            height: 96px;
            margin: 0 auto 11px auto;
            @include md{
                width: 88px;
                height: 88px;
                margin: 0;
            }
        }
        &-text{
            font-size: 20px;
            @include md{
                flex: 1;
                font-size: 18px;
            }
            @include sm{
                font-size: 14px;
            }
        }
    }
    &-video{
        margin-top: 50px;
        padding: 24px;
        box-sizing: border-box;
        gap: 26px;
        text-decoration: none;
        @media screen and (min-width: $lg) {
            margin-bottom: -280px;
            position: relative;
            z-index: 3;
        }
        @include md{
            flex-direction: column;
            gap: 20px;
        }
        &-icon{
            width: 136px;
            @include lg{
                width: 90px;
                height: 90px;
            }
        }
        &-text{
            font-size: 32px;
            font-weight: 600;
            @include lg{
                font-size: 26px;
            }
            @include sm{
                font-size: 16px;
            }
        }
    }
}
