.footer-contact li:not(:last-child), .footer-nav li:not(:last-child) {
  margin-bottom: 14px;
}

.footer-geo {
  margin-bottom: 29px;
  cursor: pointer;
}

@media (max-width: 991px) {
  .footer-geo {
    width: 35%;
  }
}

.footer-title:not(:last-child), .footer-info:not(:last-child) {
  margin-bottom: 19px;
}

.footer-payments {
  gap: 14px;
  max-width: 210px;
}

@media (max-width: 991px) {
  .footer-payments {
    margin-top: 25px;
    max-width: 100%;
  }
}

.footer-payments img {
  display: block;
  max-height: 20px;
  max-width: 60px;
}

@media (max-width: 991px) {
  .footer-item {
    margin-bottom: 25px;
  }
}

.footer-item-title {
  text-transform: uppercase;
  margin-bottom: 15px;
  font-size: 20px;
  color: var(--black);
}

@media (max-width: 991px) {
  .footer-item-content {
    display: none;
  }
}

.footer-header {
  justify-content: space-between;
}

.footer-sub {
  margin-bottom: 45px;
}

.footer-sub .input-group:not(:last-child) {
  margin-bottom: 20px;
}

.footer-meta li:not(:last-child) {
  margin-bottom: 10px;
}

.footer-meta li a {
  text-decoration: underline;
}

.footer-meta li a:hover {
  text-decoration: none;
}

.footer-toggle {
  position: relative;
  cursor: pointer;
}

.footer-toggle:after {
  content: "";
  width: 37px;
  height: 37px;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translate(0, -50%);
  background-image: url("../img/footer-toggle.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  will-change: auto;
  transition: transform ease-in-out 0.2s, background-image ease-in-out 0.2s;
}

.footer-toggle.active:after {
  background-image: url("../img/footer-toggle-active.svg");
  transform: translate(0, -50%) rotate(180deg);
}
