.button{
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 24px;
  min-height: 63px;
  font-family: inherit;
  font-size: 20px;
  font-weight: 600;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  padding: 2px 34px;
  box-sizing: border-box;
  color: var(--white);
  border-radius: 11px;
  border: 2px solid transparent;
  transition: background-color ease-in-out 0.2s, color ease-in-out 0.2s, border-color ease-in-out 0.2s;
  &-orange{
    background-color: var(--orange);
    &:hover{
      background-color: var(--red);
    }
    &-bordered{
      border-color: var(--orange);
      color: var(--orange);
      &:hover{
        border-color: var(--red);
        color: var(--red);
      }
    }
  }
  &-red{
    background-color: var(--red);
    &:hover{
      background-color: var(--orange);
    }
    &-bordered{
      border-color: var(--red);
      color: var(--red);
      &:hover{
        border-color: var(--orange);
        color: var(--orange);
      }
    }
  }

  &-small{
    min-width: 136px;
    padding-left: 10px;
    padding-right: 10px;
    min-height: 35px;
    font-size: 12px;
    border-radius: 6px;
    border-width: 1px;
  }
}

button{
  &.button{
    border: none;
    min-height: 70px;
  }
}