.calc{
    .section-desc{
        max-width: 665px;
    }
    &-img{
        padding-right: 50px;
        box-sizing: border-box;
    }
    &-box{
        padding: 62px 77px;
        box-sizing: border-box;
        @include xl{
            padding: 45px;
        }
        @include sm{
            padding: 30px;
        }
        &-row{
            --bs-gutter-y: 49px;
            @include lg{
                --bs-gutter-y: 30px;
            }
            @include sm{
                --bs-gutter-y: 20px;
            }
        }
    }
}