.popular{
    &-slider{
        max-width: 100%!important;
        overflow: inherit;
    }
    .swiper-control.prev{
        left: -65px;
    }
    .swiper-control.next{
        right: -65px;
    }
    .swiper-slide{
        opacity: 0;
        transition: opacity ease-in-out 0.2s;
    }
    @media screen and (min-width: $xl) {
        .swiper-slide-active,
        .swiper-slide-next,
        .swiper-slide-next + div{
            opacity: 1;
        }
    }
    @media screen and (min-width: $md) {
        .swiper-slide-active,
        .swiper-slide-next{
            opacity: 1;
        }
    }
    @media screen and (min-width: 0) {
        .swiper-slide-active{
            opacity: 1;
        }
    }
}



