.that{
    &-box{
        padding: 20px 30px;
        box-sizing: border-box;
        border-radius: 15px;
        gap: 30px;
        @include sm{
            gap: 20px;
        }
        &-icon{
            width: 90px;
            height: 90px;
            border-radius: 100%;
            @include sm{
                width: 60px;
                height: 60px;
                img{
                    max-height: 36px;
                    max-width: 36px;
                }
            }
        }
        &-content{
            flex: 1;
        }
        &-title{
            margin-bottom: 13px;
            font-size: 24px;
            @include xl{
                font-size: 20px;
                margin-bottom: 10px;
            }
            @include lg{
                font-size: 16px;
            }
            @include sm{
                font-size: 14px;
                margin-bottom: 5px;
            }
        }
        &-desc{}
    }
}