.header{
    position: relative;
    &-top{
        padding: 10px 0;
        box-sizing: border-box;
    }
    &-meta{
        justify-content: space-between;
        align-items: center;
        gap: 5px;
        font-size: 14px;
        &-link{
            display: flex;
            align-items: center;
            text-decoration: none;
            gap: 8px;
        }
        &-icon{
            width: 20px;
            height: 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            img,
            svg{
                max-width: 20px;
                max-height: 20px;
            }
        }
        &-text{
            flex: 1;
        }
    }
    &-user{
        width: 20px;
        height: 20px;
    }

    &-inner{
        font-size: 14px;
        &-top,
        &-bottom{
            display: flex;
            justify-content: space-between;
        }
        &-top{
            padding: 15px 0;
            align-items: center;
        }
        &-bottom{
            align-items: flex-end;
            padding-bottom: 15px;
            position: relative;
            @include lg{
                padding: 0;
            }
        }
    }

    

    &-logo{
        max-width: 170px;
        width: 100%;
        display: inline-block;
        @include lg{
            max-width: 105px;
        }
    }
    &-address{
        display: flex;
        gap: 6px;
        max-width: 185px;
        &-icon{
            width: 15px;
        }
        &-text{
            flex: 1;
        }
    }
    &-search{
        flex: 1;
        position: relative;
        margin: 0 75px 0 45px;
        &-inner{
            position: relative;
        }
        @include xl{
            margin: 0 20px;
        }
        @include lg{
            padding: 15px;
            box-sizing: border-box;
            background-color: var(--white);
            position: absolute;
            top: calc(100% + 10px);
            left: 0;
            z-index: 2;
            width: 696px;
            left: 50%;
            transform: translate(-50%, 0);
            margin: 0;
            border-radius: 10px;

            opacity: 0;
            visibility: hidden;
            transition: opacity ease-in-out 0.2s, visibility ease-in-out 0.2s;
            &.active{
                opacity: 1;
                visibility: visible;
            }
        }
        @include md{
            width: 516px;
        }
        @include sm{
            width: calc(100% - 20px);
        }
        &-input{
            width: 100%;
            border-color: var(--black-90);
            padding-right: 80px;
            transition: border-color ease-in-out 0.2s;
            &:focus{
                border-color: var(--orange);
            }
        }
        &-button{
            position: absolute;
            right: 0;
            top: 0;
            height: 100%;
            width: 55px;
            border: none;
            background-color: transparent;
            cursor: pointer;
            path{
                transition: fill ease-in 0.2s;
            }
            &:hover{
                path{
                    fill: var(--orange);
                }
            }
        }
    }
    &-contact{
        max-width: 550px;
        width: 100%;
        justify-content: space-between;
        @include xl{
            max-width: 500px;
        }
        a{
            &:not(.button){
                text-decoration: none;
                color: var(--orange);
                transition: color ease-in-out 0.2s;
                &:hover{
                    color: var(--red);
                }
            }
        }
    }

    &-socials{
        max-width: 215px;
        width: 100%;
        @include xl{
            max-width: 190px;
        }
        &-title{
            margin-bottom: 9px;
        }
    }
    &-nav{
        flex: 1;
        padding-bottom: 7px;
        @include xl{
            font-size: 12px;
        }
        @include lg{
            padding: 15px;
            box-sizing: border-box;
            background-color: var(--white);
            position: absolute;
            top: calc(100% + 10px);
            left: 0;
            z-index: 9;
            width: 696px;
            left: 50%;
            transform: translate(-50%, 0);
            margin: 0;
            border-radius: 10px;

            opacity: 0;
            visibility: hidden;
            transition: opacity ease-in-out 0.2s, visibility ease-in-out 0.2s;
            &.active{
                opacity: 1;
                visibility: visible;
            }
        }
        @include md{
            width: 516px;
        }
        @include sm{
            width: calc(100% - 20px);
        }

        &-has-child{
            &-link{
                &.active{
                    svg{
                        transform: rotate(180deg);
                    }
                    @media screen and (min-width: $lg) {
                        path{
                            fill: var(--orange);
                        }
                        color: var(--orange);
                    }
                }
            }
        }

        &-list{
            justify-content: space-between;
            @include lg{
                flex-direction: column;
            }
            & > li{
                @include lg{
                    &:not(:last-child){
                        margin-bottom: 10px;
                    }
                }
                & > a{
                    font-weight: 600;
                    text-transform: uppercase;
                    text-decoration: none;
                    transition: color ease-in-out 0.2s;
                    path{
                        transition: fill ease-in-out 0.2s;
                    }
                    &:hover{
                        color: var(--orange);
                        path{
                            fill: var(--orange);
                        }
                    }
                    &.d-flex{
                        align-items: center;
                        gap: 8px;
                    }
                    svg{
                        transition: transform ease-in-out 0.2s;
                    }
                    @include lg{
                        text-transform: none;
                    }
                }
            }
            &-child{
                @media screen and (min-width: $lg) {
                    position: absolute;
                    width: 100%;
                    top: calc(100% + 10px);
                    left: 50%;
                    transform: translate(-50%, 0);
                    background-color: var(--white);
                    border-radius: 10px;
                    box-sizing: border-box;
                    z-index: 9;
                    box-shadow: 0px 4px 9.9px 2px rgba(61, 61, 61, 0.17);
                    opacity: 0;
                    visibility: hidden;
                    transition: opacity ease-in-out 0.2s, visibility ease-in-out 0.2s;
                    &.active{
                        opacity: 1;
                        visibility: visible;
                    }
                }
                @include lg{
                    display: none;
                    margin-top: 10px;
                }
                .row{
                    .col-lg-3{
                        &:not(:last-child){
                            border-right: 1px solid var(--gray);
                        }
                    }
                }
            }
        }
        
    }


    &-toggle{
        display: block;
        margin: 0;
        padding: 0;
        border: none;
        background: none;
        position: relative;
        width: 30px;
        height: 30px;
        cursor: pointer;
        background-color: var(--black-90);
        border-radius: 3px;
        span{
            display: block;
            height: 1px;
            width: 16px;
            left: 7px;
            background-color: var(--white);
            position: absolute;
            border-radius: 5px;
            transition: transform ease-in-out 0.2s, opacity ease-in-out 0.2s, top ease-in-out 0.2s, bottom ease-in-out 0.2s;
            &:nth-child(1){
                top: 9px;
            }
            &:nth-child(2){
                width: 12px;
                top: 50%;
                transform: translate(0, -50%);
            }
            &:nth-child(3){
                bottom: 9px;
            }
        }
        &.active{
            span{
                &:nth-child(1){
                    top: 50%;
                    transform: translate(0, -50%) rotate(45deg);
                }
                &:nth-child(2){
                    opacity: 0;
                }
                &:nth-child(3){
                    bottom: inherit;
                    top: 50%;
                    transform: translate(0, -50%) rotate(-45deg);
                }
            }
        }
    }
}
.header-nav{
    @include lg{
        max-height: calc(100vh - 130px);
        overflow-y: auto;
    }
    &-title{
        margin-bottom: 18px;
        font-size: 20px;
        font-weight: 600;
        color: var(--black);
        @include xl{
            font-size: 18px;
        }
        @include lg{
            font-size: 14px;
            font-weight: normal;
            display: flex;
            align-items: center;
            gap: 8px;
            margin: 0;
        }
        svg{
            transition: transform ease-in-out 0.2s;
        }
        &.active{
            svg{
                transform: rotate(180deg);
            }
        }
    }
    &-sub{
        @include lg{
            display: none;
            margin-top: 10px;
        }
        &-item{
            padding: 15px;
            box-sizing: border-box;
            @include lg{
                padding: 0;
                margin-bottom: 10px;
            }
            li{
                &:not(:last-child){
                    margin-bottom: 20px;
                    @include lg{
                        margin-bottom: 10px;
                    }
                }
            }
        }  
        &-link{
            display: inline-flex;
            align-items: center;
            gap: 18px;
            text-decoration: none;
            color: var(--black);
            transition: color ease-in-out 0.2s;
            &:hover{
                color: var(--orange);
            }
        }
        &-icon{
            width: 40px;
            height: 40px;
            @include lg{
                display: none;
            }
            img{
                max-height: 40px;
                max-width: 40px;
            }
        }
        &-text{
            flex: 1;
        }
    }
}