.partners{
    &-row{
        &-col{
            width: calc(100% / 7);
            flex: inherit;
            @include lg{
                width: calc(100% / 5);
            }
            @include sm{
                width: calc(100% / 4);
            }
        }
    }
    &-box{
        text-align: center;
        display: block;
        &-logo{
            width: 100px;
            height: 100px;
            margin: 0 auto 18px auto;
            @include lg{
                width: 70px;
                height: 70px;
            }
            @include md{
                width: 50px;
                height: 50px;
                margin: 0 auto 10px auto;
            }
        }
    }
}