.faq{
    &-items{
        border: 1px solid var(--gray)
    }
    &-item{
        &:not(:last-child){
            border-bottom: 1px solid var(--gray)
        }
        &-header{
            font-size: 24px;
            font-weight: 600;
            cursor: pointer;
            padding: 42px 150px 42px 42px;
            box-sizing: border-box;
            position: relative;
            @include lg{
                font-size: 20px;
                padding: 30px 100px 30px 30px;
            }
            @include sm{
                padding: 15px 55px 15px 15px;
                font-size: 16px;
            }
            &:after{
                content: "";
                width: 60px;
                height: 60px;
                border: 1px solid var(--gray);
                position: absolute;
                right: 78px;
                top: 50%;
                transform: translate(0, -50%);
                pointer-events: none;
                background-image: url("../img/faq-arrow.svg");
                background-position: center;
                background-repeat: no-repeat;
                border-radius: 100%;
                will-change: transform;
                transition: transform ease-in-out 0.2s, background-image ease-in-out 0.2s;
                @include lg{
                    right: 30px;
                    width: 50px;
                    height: 50px;
                }
                @include sm{
                    width: 30px;
                    height: 30px;
                    right: 15px;
                    background-size: 10px;
                }
            }
            &.active{
                &:after{
                    background-image: url("../img/faq-arrow-active.svg");
                    transform: translate(0, -50%) rotate(180deg);
                }
            }
        }
        &-content{
            display: none;
            padding: 0 42px 42px 42px;
            box-sizing: border-box;
            @include lg{
                padding: 0 30px 30px 30px;
            }
            @include sm{
                padding: 0 15px 15px 15px;
            }
        }
    }
}