.modal{
    &-dialog{}
    &-close{
        position: absolute;
        z-index: 2;
        border-radius: 100%;
        cursor: pointer;
        background-color: transparent;
        border: none;
        width: 30px;
        height: 30px;
        top: 13px;
        right: 13px;
        padding: 0;
        path,
        rect{
            transition: fill ease-in-out 0.2s, stroke ease-in-out 0.2s;
        }
        &:hover{
            path{
                fill: var(--red);
            }
            rect{
                stroke: var(--red);
            }
        }
    }
    &-header{
        margin-bottom: 50px;
        border: none;
        padding: 0;
        text-align: center;
        display: block;
        &-title{
            font-size: 32px;
            @include lg{
                font-size: 26px;
            }
        }
    }
    &-content{
        padding: 65px 50px;
        box-sizing: border-box;
        border: none;
        box-shadow: none;
        border-radius: 20px;
        overflow: hidden;
        @include sm{
            padding: 55px 30px 30px 30px;
        }
    }


    &-geo{
        &-header{
            background-color: var(--gray-90);
            padding: 30px 23px;
            box-sizing: border-box;
            &-tag{
                margin-bottom: 8px;
                font-size: 14px;
            }
            &-title{
                font-size: 16px;
                font-weight: 600;
            }
        }
        &-content{
            padding: 20px 23px;
            box-sizing: border-box;
            font-size: 14px;
        }
        &-list{
            column-count: 2;
            li{
                &:not(:last-child){
                    margin-bottom: 10px;
                }
                a{
                    text-decoration: none;
                    transition: color ease-in-out 0.2s;
                    &:hover{
                        color: var(--orange);
                    }
                }
            }
        }
    }
    &-contact{
        font-size: 14px;
        padding: 25px;
        box-sizing: border-box;
        &-header{
            margin-bottom: 16px;
            font-weight: 600;
        }
        &-title{
            color: var(--black);
            margin-bottom: 7px;
        }
        &-desc{}

        &-items{}

        &-item{
            display: flex;
            align-items: center;
            gap: 6px;
            text-decoration: none;
            &:not(:last-child){
                margin-bottom: 10px;
            }
            &-icon{
                width: 20px;
                height: 20px;
            }
            &-text{
                flex: 1;
            }
        }
    }
    
}